
import { trigger, state, style, animate, transition } from '@angular/animations';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AppService } from '../../../services/app.service';
import { BaseComponent } from '../../../shared/globals/base.component';

@Component({
    selector: 'locker-display',
    templateUrl: './display.template.html',
    styleUrls: ['./display.styles.scss'],
    animations: [
        trigger('show', [
            state('show', style({ opacity: 1, width: '*', height: '*' })),
            state('hide', style({ opacity: 0, width: 0, height: 0 })),
            transition('show <=> hide', animate('300ms ease-in')),
        ])
    ]
})
export class LockerDisplayComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public level: string;
    @Input() public section: string;
    @Input() public type: string;
    @Output() public sectionChange = new EventEmitter();
    @Output() public book = new EventEmitter();
    @Output() public event = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute) {
        super();
    }

    public ngOnInit() {
        this.init();
    }

    public ngOnChanges(changes: any) {
        if (changes.level || changes.section || changes.type) {
            this.loadLockers();
        }
    }

    public init() {
        if (!this.service.ready()) {
            return setTimeout(() => this.init(), 500);
        }
        if (localStorage) {
            this.model.pin = localStorage.getItem('STAFF.lockers.pin');
        }
        this.interval('lockers', () => this.updateLockersState(), 5 * 60 * 1000);
        this.subscription('bld', this.service.Buildings.listen((bld) => {
            if (bld) {
                this.model.locker_list = null;
                this.model.lockers = bld.lockers;
                this.loadLockers();
            }
        }));
        this.model.colors = this.service.Settings.get('app.colors.lockers') || {};
        if (!this.model.colors.available) { this.model.colors.available = '#91d2cc'; }
        if (!this.model.colors.unavailable) { this.model.colors.unavailable = '#ccc'; }
        if (!this.model.colors.active) { this.model.colors.active = '#e17829'; }
        if (!this.model.colors['available-border']) { this.model.colors['available-border'] = '#80c1bb'; }
        if (!this.model.colors['unavailable-border']) { this.model.colors['unavailable-border'] = '#bbb'; }
        if (!this.model.colors['active-border']) { this.model.colors['active-border'] = '#d06718'; }
        this.subscription('lockers', this.service.Lockers.listen('map', (map) => {
            this.model.locker_map = map;
        }));
    }

    public loadLockers() {
        if (this.level && this.section && this.model.lockers) {
            const lvl = this.model.lockers[this.level];
            this.model.level = this.service.Buildings.getLevel(this.level);
            if (lvl) {
                const section = lvl[this.section];
                if (section) {
                    this.model.locker_list = section[this.type || 'personal'];
                } else {
                    this.section = '';
                    this.sectionChange.emit('');
                }
            } else {
                this.section = '';
                this.sectionChange.emit('');
            }
        }
        this.updateLockersState();
    }

    public updateLockersState() {
        if (this.model.locker_list) {
            const list = this.model.locker_list.flat();
            let first = null;
            let last = null;
            for (const id of list) {
                if (!first) { first = id; }
                if (id) { last = id; }
            }
            if (first && last) {
                this.model.loading = true;
                this.service.Lockers.query({ locker_from: first, locker_to: last }).then((lockers) => {
                    this.model.loading = false;
                    this.model.locker_map = {};
                    for (const lkr of lockers) {
                        this.model.locker_map[lkr.id] = {
                            id: lkr.id,
                            serial: lkr.serial,
                            available: lkr.available,
                            pin: lkr.pin,
                            card: lkr.card
                        };
                    }
                }, () => this.model.loading = false);
            }
        }
    }

}
