
import { Component, Output, EventEmitter, OnInit, Input } from '@angular/core';

import { AppService } from '../../../services/app.service';
import { BaseComponent } from '../../../shared/globals/base.component';
import { ActivatedRoute } from '@angular/router';

import * as moment from 'moment';

@Component({
    selector: 'locker-search',
    templateUrl: './search.template.html',
    styleUrls: ['./search.style.scss']
})
export class LockerSearchComponent extends BaseComponent implements OnInit {
    @Input() public type: string;
    @Output() public event = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute) {
        super();
    }

    public ngOnInit() {
        this.subscription('route', this.route.paramMap.subscribe((params) => {
        }));
        this.subscription('bld', this.service.Buildings.listen((bld) => {
            if (bld) {
                this.model.level_list = [];
                this.model.lockers = bld.lockers;
                for (const lvl of bld.levels) {
                    if (this.model.lockers[lvl.id]) {
                        const lvl_lockers = this.model.lockers[lvl.id] || {};
                        const section_ids = Object.keys(lvl_lockers);
                        const sections = [];
                        const section_names = [];
                        for (const key of section_ids) {
                            const list = ((lvl_lockers[key] || {})[this.type || 'personal'] || []).flat();
                            let available = 0;
                            let count = 0;
                            for (const item of list) {
                                if (item) {
                                    count++;
                                    const i = this.service.Lockers.get('map')[item];
                                    if (i && i.available) { available++; }
                                }
                            }
                            if (section_names.indexOf(key) < 0) {
                                section_names.push(key);
                            }
                            sections.push({
                                name: key,
                                available,
                                count
                            });
                        }
                        this.model.level_list.push({
                            id: lvl.id,
                            name: lvl.name,
                            number: lvl.number,
                            lockers: this.model.lockers[lvl.id],
                            sections
                        });
                        this.model.filters = {
                            levels: this.model.level_list,
                            sections: section_names
                        };
                    }
                }
            }
        }));
    }

    public select(lvl: string, section: string) {
        this.model.level = lvl;
        this.model.section = section;
        this.model.filters.level = { id: lvl };
        this.model.filters.section = section;
        this.model.filters = JSON.parse(JSON.stringify(this.model.filters));
    }

    public update() {
        const filters = this.model.filters;
        if (filters.level && filters.section) {
            this.select(filters.level.id, filters.section);
        }
    }

    public book(item) {
        const date = moment(this.model.filters ? this.model.filters.date || '' : '').hours(12).minutes(0);
        const end = moment(this.model.filters ? this.model.filters.end || '' : '').hours(12).minutes(0);
        this.model.loading = true;
        this.service.Lockers.book(item.id, { start: date.valueOf(), end: end.valueOf() }, item.pin).then(
            (pin) => {
                this.model.success = true;
                this.model.pin = pin;
                if (localStorage) {
                    localStorage.setItem('STAFF.lockers.pin', pin);
                }
                // this.timeout('booked', () => this.service.navigate(['lockers']), 5000);
            }, (err) => {
                this.model.loading = false;
                this.service.error('Failed to book locker');
            });
    }

    public back() {
        this.service.navigate(['lockers']);
    }
}
