
import { trigger, state, style, animate, transition } from '@angular/animations';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AppService } from '../../../../services/app.service';
import { BaseComponent } from '../../../../shared/globals/base.component';
import { ILevel } from '../../../../services/data/buildings.service';

import * as moment from 'moment';

export interface ISearchFilters {
    date?: number;
    end?: number;
    level: ILevel;
    levels: ILevel[];
    section: any;
    sections: any[];
}

@Component({
    selector: 'locker-search-filters',
    templateUrl: './filters.template.html',
    styleUrls: ['./filters.styles.scss'],
    animations: [
        trigger('show', [
            state('show', style({ opacity: 1, width: '*', height: '*' })),
            state('hide', style({ opacity: 0, width: 0, height: 0 })),
            transition('show <=> hide', animate('300ms ease-in')),
        ])
    ]
})
export class LockerSearchFiltersComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public filters: ISearchFilters;
    @Output() public filtersChange = new EventEmitter();
    @Output() public back = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService, private route: ActivatedRoute) {
        super();
    }

    public ngOnInit() {
        this.init();
    }

    public init() {
        if (!this.service.ready()) {
            return setTimeout(() => this.init(), 500);
        }
        this.model.show_simple = this.service.Settings.get('app.lockers.simple_filters');
        this.selectDateRange();
        // this.model.floor = this.model.floors[0];
    }

    public ngOnChanges(changes: any) {
        if (changes.filters) {
            if (!this.filters) { this.filters = {} as ISearchFilters; }
            this.model.level_list = this.filters.levels || [];
            this.model.level_names = [];
            for (const lvl of this.model.level_list) {
                if (this.filters.level && (lvl.id === this.filters.level || lvl.id === this.filters.level.id)) {
                    this.model.level = this.model.level_list.indexOf(lvl);
                }
                this.model.level_names.push(lvl.name);
            }
            this.model.lvl = this.model.level_list[this.model.level];
            this.model.section_list = this.filters.sections || [];
            this.model.section = -1;
            for (const s of this.model.section_list) {
                if ((s.id && s.id === this.filters.section) || s === this.filters.section) {
                    this.model.section = this.model.section_list.indexOf(s);
                    break;
                }
            }
            this.updateDisplay();
        }
    }

    public selectDateRange() {
        const now = moment();
        now.minutes(Math.ceil(now.minutes() / 5) * 5).seconds(0).milliseconds(0);
        if (!this.model.date) {
            this.model.date = now.valueOf();
        }
        const date = moment(this.model.date);
        this.service.Overlay.openModal('date-period', { data: { start: this.model.date, end: this.model.end } })
            .then((inst: any) => inst.subscribe((event) => {
                if (event.type === 'Accept') {
                    if (event.data.form && event.data.start) {
                        const new_start = moment(event.data.form.start).hours(date.hours()).minutes(date.minutes())
                            .seconds(0).milliseconds(0);
                        const new_end = moment(event.data.form.end).hours(date.hours()).minutes(date.minutes())
                            .seconds(0).milliseconds(0);
                        this.model.date = new_start.valueOf();
                        this.model.end = new_end.valueOf();
                        this.updateDisplay();
                    }
                    this.post();
                }
                event.close();
            }));
    }

    public selectLevel() {
        this.service.Overlay.openModal('item-select', { data: {
            name: 'Level',
            options: this.model.level_names,
            selected: this.model.level
        } }).then((inst: any) => inst.subscribe((event) => {
            if (event.type === 'Select') {
                this.model.level = event.data.selected;
                this.model.lvl = this.model.level_list[this.model.level];
                this.post();
            }
            event.close();
        }));
    }

    public selectSection() {
        this.service.Overlay.openModal('item-select', { data: {
            name: 'Section',
            options: this.model.section_list,
            selected: this.model.section
        } }).then((inst: any) => inst.subscribe((event) => {
            if (event.type === 'Select') {
                this.model.section = event.data.selected;
                this.post();
            }
            event.close();
        }));
    }

    public changeFilters() {
        const rooms = this.model.rooms;
        delete this.model.rooms;
        this.service.Overlay.openModal('room-filter', { data: JSON.parse(JSON.stringify(this.model)) })
            .then((inst: any) => inst.subscribe((event) => {
                if (event.type === 'Update') {
                    const data = event.data;
                    const form = data.form;
                    this.model.level.index = event.data.level.index;
                    this.model.capacity.index = event.data.capacity.index;
                    this.model.show_all = form ? form.in_use || form.show_all : data.in_use || data.show_all;
                    this.model.extras = event.data.extras;
                    this.post();
                } else {
                    event.close();
                }
            }));
        this.model.rooms = rooms;
    }

    public updateDisplay() {
        const date = moment(this.model.date);
        const end = moment(this.model.end || this.model.date);
        const now = moment();

        date.minutes(Math.ceil(date.minutes() / 5) * 5);
        this.model.display_time = date.format('h:mma');
        const d = Math.floor(moment.duration(end.diff(date)).asDays() + 1);
        this.model.display_date = `${d || 1} day${!d || d === 1 ? '' : 's'} starting ${now.isSame(date, 'd') ? 'Today' : date.format('DD/MM/YYYY')}`;
    }

    public post() {
        this.updateDisplay();
        this.filters = {
            date: this.model.date,
            end: this.model.end,
            levels: this.filters.levels,
            level: this.model.lvl,
            section: this.model.section_list[this.model.section],
            sections: this.filters.sections
        };
        this.filtersChange.emit(JSON.parse(JSON.stringify(this.filters)));
    }

}
