
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { AppService } from '../../services/app.service';
import { BaseComponent } from '../../shared/globals/base.component';

@Component({
    selector: 'app-lockers',
    templateUrl: './lockers.template.html',
    styleUrls: ['./lockers.styles.scss']
})
export class LockersComponent extends BaseComponent implements OnInit {
    public model: any = [];
    public timers: any = {};

    constructor(private route: ActivatedRoute, private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.service.set('BANNER.block_height', 0);
        this.subscription('route', this.route.paramMap.subscribe((params) => {
            if (params.has('type')) {
                this.model.type = params.get('type');
            }
        }));
    }

    public select(type: string) {
        this.service.navigate(['lockers', type.toLowerCase()]);
    }
}
